* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "poppins", sans-serif;
}

.App {
  font-family: "poppins", sans-serif;
}

nav {
  height: 70px;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.bold {
  font-weight: bold;
}

.gray {
  color: #918e9b;
}

.nav--logo {
  width: 60px;
  height: 50px;
  padding-bottom: 10px;
}

section {
  padding: 20px;
}

.hero {
  display: flex;
  flex-direction: column;
}

.hero--photo {
  max-width: 400px;
  align-self: center;
}

.hero--header {
  margin-bottom: 16px;
}

.hero--text {
  margin-top: 0;
}

nav > h2 {
  padding: 0px;
  margin: 0;
  color: #918e9b;
}

#work {
  font-weight: bold;
  font-size: 18px;
}

.card {
  margin: auto;
  /* margin-left: 50px; */
  display: flex;
  margin-top: 40px;
  margin-bottom: 15px;
  /* justify-content: center; */
  /* align-items: center; */
  /* background-color: ghostwhite; */
  width: 750px;
  /* height: 250px; */
  /* align-items: center; */
}

.card--img {
  position: relative;
  width: 200px;
  height: 280px;
  margin: auto;
  margin-right: 20px;
  margin-left: 10px;
  border-radius: 8px;
}

.card--img:hover {
  position: relative;
  border: 4px solid blueviolet;
  width: 250px;
  height: 300px;
  margin: auto;
  margin-right: 20px;
  margin-left: 10px;
  border-radius: 8px;
}

h1 {
  font-weight: bold;
}
.card--into {
  /* justify-content: center; */
}

.div--text {
  max-width: 400px;
}

hr {
  width: 748px;
}

#a--href {
  color: #918e9b;
  text-decoration: none;
}

#card--href {
  color: black;
  text-decoration: none;
}

hr {
  color: red;
}
